	module.exports = {"data":{"markdownRemark":{"html":"<p>I’m currently working on some volunteer instructional design projects. There’ve been some bumps in the road, but for the most part, the project started well. There was a general feeling that by revamping the more fragmenting onboarding process (that’s what I’m helping with), there was an opportunity to create something much more interactive and relevant. I came into the project with my instructional designer hat on, excited to continue on my learning journey using Action Mapping and SAM. So, of course, my very first question was, “by the end of this course, what do your users need to be able to do?”</p>\n<h2>Battlefield ID</h2>\n<p>Over time, as we’ve continued to explore, there’s been a large change in the project scope.What started as a simple, single module morphed into a much larger multi-module course. I can certainly understand this growth and change during this exploration process. I made sure to keep an eye on scope and had to inform the organization that I wouldn’t be able to commit to doing, essentially the amount of work I did at my full-time, 8 dedicated hours a day job, in the space of maybe 10 dedicated hours a week. We got over that one with a bit of conversation but now, an even more serious issue has arisen. The storyboards I’m producing are going around through several stakeholders and now, more and more of a content-focus is being pushed. The orders have come to put more stuff that that users “need to know” in the course. This is a much tougher problem because I can see the course moving straight ahead down “text-and-next” alley. Unlike a hired consultant (which, to be frank, is probably what this project needs) I’m a humble volunteer. And, much like in my day job, I don’t have a ton of (or any) power over whether or not a course needs to be created or whether other avenues have been explored. Still, I figured that they came to me as an instructional designer and I needed to push back. Here are the questions I asked:</p>\n<ul>\n<li>Does the content need to all be in this module?</li>\n<li>Does the content need to be in the course at all? How does it affect the user’s ability to do their job?</li>\n<li>Have you considered the wholistic onboarding process and other activities that you might run in parallel to a course to disseminate information?</li>\n</ul>\n<h2>Real World</h2>\n<p>I haven’t yet heard a follow up, but I’m not optomistic. I would imagine that even hired freelancers, unless they felt willing and able to fire a client, might have to submit to putting on their information design hats, instead, and settle on making the course palatable by including whatever slick and interesting interactions they could dream up that break up the monotony of text.</p>\n<blockquote>\n<p>I’d rather be an employed elearning compromiser than an unemployed elearning purist.</p>\n<p>– Tom Kuhlmann</p>\n</blockquote>\n<p>I remember very vividly when I first jumped into instructional design, reading all of the advice of the established gurus and thinking (somewhat bitterly) that when the real world intervened, I was expected to perform the task I was given. (To be honest, it was one of the many reasons I was drawn to the Articulate blogs and forums. They seemed to be one of the only places talking about things like this with a community of folks in positions like me. <a href=\"http://blogs.articulate.com/rapid-elearning/3-simple-ways-to-measure-the-success-of-your-e-learning/\">Tom Kuhlmann even wrote that nifty quote above.</a>) I’ve certainly learned to ask more questions, learned to push back on strategies and ideas that don’t work, but at the end of the day, I’m the captive developer; I’ve got to do the job, whether or not I’ve been able to convince others to go in a different direction. Fellow instructional designers, freelance and otherwise, especially you fellow newbies out there, how do you fight this battle? How often do you win? What do you do when what is required of you is information design instead of instructional design? I’d love to hear from you in the comments.</p>","frontmatter":{"date":"May 04, 2015","path":"/blog/instructional-design-battlefield-information-vs-instructional-design","title":"Instructional Design Battlefield: Information vs Instructional Design","tags":["information design","instructional design","problems"],"categories":["Design"],"featured":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":2,"src":"/static/Instructional-Design-Battlefield-c33301e5d861c2fb58c51f7f7a1ab8fc-81d39.png","srcSet":"/static/Instructional-Design-Battlefield-c33301e5d861c2fb58c51f7f7a1ab8fc-9a8d1.png 203w,\n/static/Instructional-Design-Battlefield-c33301e5d861c2fb58c51f7f7a1ab8fc-c53f9.png 405w,\n/static/Instructional-Design-Battlefield-c33301e5d861c2fb58c51f7f7a1ab8fc-81d39.png 810w,\n/static/Instructional-Design-Battlefield-c33301e5d861c2fb58c51f7f7a1ab8fc-fda19.png 1024w","sizes":"(max-width: 810px) 100vw, 810px"}}}}}},"pathContext":{}}

